@media screen and (min-width: 320px) and (max-width: 800px) {
  .light-header {
    background-color: #01002E;
    color: #ED6112;
  }

  .dark-header {
    background-color: #ED6112;
    color: #01002E;
  }

  .light-svg {
    filter: invert(40%) sepia(65%) saturate(2333%) hue-rotate(358deg) brightness(99%) contrast(89%);
  }

  #header {
    padding: 1.5vh 3vw 1.5vh 2vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 4vh;
  }

  #header h3 {
    margin: 0;
  }

  #header button {
    background: none;
    color: inherit;
    border: none;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    margin-left: 12px;
    padding: 0;
  }

  #header button svg {
    width: 6vw;
    height: 4vh;
    max-width: 30px;
    max-height: 30px;
    min-width: 25px;
    min-height: 25px;
  }

  #header-buttons {
    display: flex;
    justify-content: flex-end;
    flex-wrap: nowrap;
    margin-left: 5px;
  }
}

